.slider {
	position: relative;
    overflow: hidden;
    margin-top: 10px;
	height: 65vh;
	width: 100%;
}

.text-slide {
	font-weight: 400;
	letter-spacing: 14 !important;
	display: inline-block;
	text-transform: uppercase;
}

.slide {
	position: absolute;
	top: 0;
	left: 0;
	width: 100%;
	height: 100%;
	opacity: 0;
	transition: opacity 0.4 ease-in-out;
	&.current {
		opacity: 1;
	}

	&:first-child {
		background: linear-gradient(217deg, rgba(0, 0, 0, 0.4), rgba(0, 0, 0, 0.4)),
			url("../../asset/img/Slide/slide0.jpg") no-repeat center center/cover;
	}
	&:nth-child(2) {
		background: linear-gradient(217deg, rgba(0, 0, 0, 0.4), rgba(0, 0, 0, 0.4)),
			url("../../asset/img/Slide/slide1.jpg") no-repeat center center/cover;
	}
	&:nth-child(3) {
		background: linear-gradient(217deg, rgba(0, 0, 0, 0.4), rgba(0, 0, 0, 0.4)),
			url("../../asset/img/Slide/slide2.jpg") no-repeat center center/cover;
	}
	&:nth-child(4) {
		background: linear-gradient(217deg, rgba(0, 0, 0, 0.4), rgba(0, 0, 0, 0.4)),
			url("../../asset/img/Slide/slide3.jpg") no-repeat center center/cover;
	}

	& .content1 {
		position: absolute;
		opacity: 0;
		top: 10px;
		right: -1000px;
		width: 100%;
	}

	&.current .content1 {
		opacity: 1;
		transform: translateX(-980px);
		transition: all 0.7s ease-in-out 0.3s;
	}

	& .content2 {
		position: absolute;
		opacity: 0;
		top: -200px;
		left: 30%;
		width: 100%;
	}

	&.current .content2 {
		opacity: 1;
		transform: translateY(200px);
		transition: all 0.7s ease-in-out 0.3s;
	}

	& .content3 {
		position: absolute;
		opacity: 0;
		top: -10px;
		left: 1000px;
		width: 100%;
	}

	&.current .content3 {
		opacity: 1;
		transform: translateX(-500px);
		transition: all 0.7s ease-in-out 0.3s;
	}

	& .content4 {
		position: absolute;
		opacity: 0;
		top: -200px;
		left: 30%;
		width: 100%;
	}

	&.current .content4 {
		opacity: 1;
		transform: translateY(200px);
		transition: all 0.7s ease-in-out 0.3s;
	}
}

button#next {
	position: absolute;
	top: 40%;
	right: 240px;
}

button#prev {
	position: absolute;
	top: 40%;
	left: 240px;
}

.button button {
	border: 2px solid whitesmoke;
	background-color: transparent;
	color: whitesmoke;
	border-radius: 50%;
	outline: none;
	cursor: pointer;
	padding: 13px 15px;

	&:hover {
		background-color: rgba(233, 153, 5, 0.877);
	}
}
